import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { RegisterComponent } from './register/register.component';
import { LoginComponent } from './login/login.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { OpenProjectsComponent } from './open-projects/open-projects.component';
import { AboutUsComponent } from './about-us/about-us.component';
import { AuthGuard } from '../services/auth.guard';
import { PrivateRegisterComponent } from './private-register/private-register.component';

const routes: Routes = [
  { component: HomeComponent, path: 'nl' },
  { component: RegisterComponent, path: 'register' },
  { component: LoginComponent, path: 'login' },
  { component: DashboardComponent, path: 'dashboard/{userGuid}', canActivate: [AuthGuard] },
  { component: OpenProjectsComponent, path: 'projects', canActivate: [AuthGuard] },
  { component: AboutUsComponent, path: 'about' },
  { component: PrivateRegisterComponent, path: 'register/private' },
];

const languages = [ //{lang}
  'NL', //Dutch
  'EN', //English
  'DE', //German
  'FR', //French
  'ES', //Spanish
  'IT', //Italian
  'RU', //Russian
  'UK', //Ukranian
  'HI', //Hindi
  'CN', //Chinese
  'KO', //Korean
  'JP', //Japanese
]

@NgModule({
  imports: [RouterModule.forRoot(routes, { paramsInheritanceStrategy: 'always' })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
