import { BrowserModule } from '@angular/platform-browser';
import { FormsModule } from '@angular/forms';
import { NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { CommonModule } from '@angular/common';
//import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { PrivateRegisterComponent } from './private-register/private-register.component';
import { HomeComponent } from './home/home.component';
import { ProjectComponent } from './project/project.component';
import { OpenProjectsComponent } from './open-projects/open-projects.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { AboutUsComponent } from './about-us/about-us.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MatTabsModule } from '@angular/material/tabs';
import { AuthInterceptor } from '../interceptors/authInterceptor';
import { LanguageInterceptor } from '../interceptors/languageInterceptor';
 
@NgModule({
  declarations: [
    AppComponent,
    LoginComponent,
    RegisterComponent,
    PrivateRegisterComponent,
    HomeComponent,
    ProjectComponent,
    OpenProjectsComponent,
    DashboardComponent,
    AboutUsComponent,
  ],
  imports: [
    BrowserModule,
    FormsModule,
    AppRoutingModule,
    HttpClientModule,
    MatTabsModule,
    //TranslateModule.forRoot(),
    CommonModule,
    BrowserAnimationsModule,
  ],
  exports: [
    //TranslateModule
  ],
  providers: [
    {
      provide: HTTP_INTERCEPTORS,
      useClass: AuthInterceptor,
      multi: true,
    }, {
      provide: HTTP_INTERCEPTORS,
      useClass: LanguageInterceptor,
      multi: true,
    }],
  bootstrap: [AppComponent]
})
export class AppModule { }
